.profile_wrapper {
   font-size: 13px;
   display: flex;
   width: 100%;
}

.left_block {
   display: flex;
   flex-direction: column;
   width: 40%;
}

.profile_photo {
   display: flex;
   padding: 15px;
   height: auto;
   background: white;
   box-shadow: 0 4px 5px -2px rgba(0, 0, 0, .22);
   box-shadow: 0 0 1px 0;
}

.profile_photo img {
   width: 100%;
   position: relative; /* If you want text inside of it */
}

.profile_wrapper .profile_info {
   display: flex;
   flex-direction: column;
   align-self: start;
   margin-left: 10px;
   height: auto;
   padding: 10px;
   width: 60%;
   background: white;
   box-shadow: 0 0 1px 0;
}

.profile_wrapper .profile_info .user_name {
   font-size: 20px;

   display: flex;

   max-height: 25px;
   padding: 5px;
}

.profile_wrapper .profile_info .status {
   display: flex;

   height: auto;
   max-height: 50px;
   margin: 5px;
   padding: 5px 0 5px 0;

   border-bottom: 1px solid #aeaeae;

}

.profile_wrapper .profile_info .descriptions_wrapper {
   display: flex;
   flex-direction: column;
   padding: 5px;
}


